import * as React from 'react';
import { TouchableWithoutFeedback, View } from 'react-native';
import { DeepPartial } from 'ts-essentials';

import { Theme, withTheme } from '../../theme';
import { mergeStyles, ReplaceReturnType } from '../../utils/mergeStyles';
import {
  GetOverlayStyles,
  getOverlayStyles,
  OverlayStyles,
} from './Overlay.styles';

interface OverlayProps {
  onPress: () => void;
  theme: Theme;
  transparent?: boolean;
  getStyles?: ReplaceReturnType<GetOverlayStyles, DeepPartial<OverlayStyles>>;
}

const OverlayBase = (props: OverlayProps) => {
  const { onPress, transparent = false, theme, getStyles } = props;

  const { overlayStyle } = mergeStyles(getOverlayStyles, getStyles)(
    { transparent },
    theme,
  );

  return (
    <TouchableWithoutFeedback
      onPress={event => {
        event.preventDefault();
        onPress();
      }}
    >
      <View style={overlayStyle} />
    </TouchableWithoutFeedback>
  );
};

export const Overlay = withTheme(OverlayBase);
try {
    // @ts-ignore
    Overlay.displayName = "Overlay";
    // @ts-ignore
    Overlay.__docgenInfo = { "description": "", "displayName": "Overlay", "props": { "onPress": { "defaultValue": null, "description": "", "name": "onPress", "required": true, "type": { "name": "() => void" } }, "transparent": { "defaultValue": null, "description": "", "name": "transparent", "required": false, "type": { "name": "boolean" } }, "getStyles": { "defaultValue": null, "description": "", "name": "getStyles", "required": false, "type": { "name": "ReplaceReturnType<GetOverlayStyles, DeepPartial<OverlayStyles>>" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/components/Overlay/Overlay.tsx#Overlay"] = { docgenInfo: Overlay.__docgenInfo, name: "Overlay", path: "src/components/Overlay/Overlay.tsx#Overlay" };
}
